import React from 'react'
import getContentPerTypeService from '../../services/content-converter-service'

class DisplayStrong extends React.Component {
    constructor(props) {
        super(props)

        /* Pour ajouter de nouveaux lments dans les strong, il suffit de les ajouter  la suite de ce tableau
         * puis de crer le composant correspondant dans le render */
        this.typeList = ['u']
    }

    render() {
        const { content } = this.props

        const contentPerType = getContentPerTypeService(this.typeList, content)

        return (
            <>
            {contentPerType.map((contentPT, index) => {
                return (() => {
                    switch (contentPT.type) {
                        case this.typeList[0]:
                            return <strong key={index}><u>{contentPT.value}</u></strong>
                        default:
                            // default correspont  strong sans rien en plus :)
                            return <strong key={index}>{contentPT.value}</strong>
                    }
                })()
            })}
            </>
        )
    }
}

export default DisplayStrong